.login-main{
  margin-top: 0px !important;
}
.login_Error {
  color: red;
  font-size: 12px;
}
.login-label{
  font-size: 13px !important;
}
.login-btn-container {
  width: 100%;
  margin-top: 10px;
  .button {
    font-size: 16px;
  }
}
.Select-padding{
  padding-left: 10px;
}
.checker {
  display: block;
  margin-top: 10px;
  color: rgb(84, 84, 84);
  .inputCheck {
    width: 29%;
    padding: 10px;
    text-align: center;
    margin: 8px 8px 0px 8px;
  }
  .inputCheck:first-child {
    margin-left: 0px !important;
  }
  .inputCheck:last-child {
    margin-right: 0px !important;
  }
}
